import { Inject, Injectable, Optional } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';
import { TenantListGuard } from './tenant-list/tenant-list.guard';
import { TenantsModuleConfig, TENANTS_MODULE_CONFIG } from './tenants.model';

@Injectable()
export class TenantsNavigationFactory implements NavigatorNodeFactory {
  private navs: NavigatorNode[] = [];

  constructor(
    private tenantListGuard: TenantListGuard,
    @Optional() @Inject(TENANTS_MODULE_CONFIG) private config: TenantsModuleConfig
  ) {}

  async get(): Promise<NavigatorNode[]> {
    const canActivateTenantList = await this.tenantListGuard.canActivate();
    if (!this.navs.length) {
      const subtenantsNavigatorNode = this.config?.subtenantsNavigatorNode ?? true;
      if (subtenantsNavigatorNode !== false) {
        this.navs.push(
          new NavigatorNode({
            parent: {
              label: gettext('Tenants'),
              icon: 'c8y-layers'
            },
            label: gettext('Subtenants'),
            icon: 'c8y-sub-tenants',
            path: 'tenants',
            routerLinkExact: false,
            priority: 4000,
            hidden: !canActivateTenantList,
            ...(subtenantsNavigatorNode === true ? {} : subtenantsNavigatorNode)
          })
        );
      }
    }
    return this.navs;
  }
}
