import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { TranslateService } from '@ngx-translate/core';
import { BehaviorSubject, from } from 'rxjs';
import { expand, reduce, shareReplay, takeWhile } from 'rxjs/operators';
import { saveAs } from 'file-saver';

import {
  ICurrentTenant,
  IResultList,
  ITenant,
  TenantService,
  TenantStatus,
  UserService
} from '@c8y/client';
import {
  ActionControl,
  AlertService,
  AppStateService,
  BuiltInActionType,
  Column,
  SortOrder,
  DisplayOptions,
  gettext,
  ModalService,
  Pagination,
  Status,
  TenantUiService,
  PasswordService
} from '@c8y/ngx-components';

import { StatusFilteringFormRendererComponent } from './status.filtering-form-renderer.component';
import { CreationTimeFilteringFormRendererComponent } from './creation-time.filtering-form-renderer.component';

@Component({
  selector: 'c8y-tenant-list',
  templateUrl: './tenant-list.component.html'
})
export class TenantListComponent implements OnInit {
  tenants$: BehaviorSubject<ITenant[]> = new BehaviorSubject(undefined);
  currentTenant: ICurrentTenant;
  isManagementTenant: boolean;
  TOP_TENANT_NAME = 'management';

  title: string = null;
  loadMoreItemsLabel: string = gettext('Load more tenants');
  loadingItemsLabel: string = gettext('Loading tenants…');

  displayOptions: DisplayOptions = {
    bordered: false,
    striped: true,
    filter: true,
    gridHeader: true
  };

  columns: Column[] = this.getColumns();
  pagination: Pagination = this.getPagination();
  showSearch = true;
  actionControls: ActionControl[] = this.getActionControls();

  TenantStatus = TenantStatus;

  constructor(
    public appState: AppStateService,
    private alertService: AlertService,
    private modalService: ModalService,
    private translateService: TranslateService,
    private tenantService: TenantService,
    private tenantUiService: TenantUiService,
    private location: Location,
    private passwordService: PasswordService,
    private userService: UserService
  ) {}

  async ngOnInit() {
    this.currentTenant = this.appState.currentTenant.value;
    this.isManagementTenant = await this.tenantUiService.isManagementTenant();
    this.loadTenants();
  }

  loadTenants() {
    this.tenants$.next(undefined);
    from(this.tenantService.list({ pageSize: 2000, withTotalPages: true, withApps: false }))
      .pipe(
        expand(resultList => resultList.paging.nextPage !== null && resultList.paging.next()),
        takeWhile(resultList => resultList.paging.nextPage !== null, true),
        reduce(
          (tenants: ITenant[], resultList: IResultList<ITenant>) => [
            ...tenants,
            ...resultList.data
          ],
          []
        ),
        shareReplay(1)
      )
      .subscribe(tenants => this.tenants$.next(tenants));
  }

  getColumns(): Column[] {
    return [
      {
        name: 'company',
        header: gettext('Tenant'),
        path: 'company',
        filterable: true,
        sortable: true,
        sortOrder: 'asc' as SortOrder
      },
      {
        name: 'id',
        header: gettext('ID'),
        path: 'id',
        filterable: true,
        sortable: true
      },
      {
        name: 'domain',
        header: gettext('Domain'),
        path: 'domain',
        filterable: true,
        sortable: true
      },
      {
        name: 'parent',
        header: gettext('Parent tenant'),
        path: 'parent',
        filterable: true,
        sortable: true
      },
      {
        name: 'contactName',
        header: gettext('Contact name'),
        path: 'contactName',
        filterable: true,
        sortable: true
      },
      {
        name: 'creationTime',
        header: gettext('Created'),
        path: 'creationTime',
        filterable: true,
        filteringFormRendererComponent: CreationTimeFilteringFormRendererComponent,
        sortable: true
      },
      {
        name: 'externalReference',
        header: gettext('External reference'),
        path: 'customProperties.externalReference',
        filterable: true,
        sortable: true
      },
      {
        name: 'status',
        header: gettext('Status'),
        path: 'status',
        filterable: true,
        filteringFormRendererComponent: StatusFilteringFormRendererComponent,
        sortable: true,
        resizable: false
      }
    ];
  }

  getPagination(): Pagination {
    return {
      pageSize: 10,
      currentPage: 1
    };
  }

  getActionControls(): ActionControl[] {
    return [
      {
        type: BuiltInActionType.Edit,
        text: gettext('Edit`tenant`'),
        callback: tenant => this.goToDetails(tenant)
      },
      {
        type: 'activateTenantAction',
        icon: 'power-off',
        text: gettext('Activate`tenant`'),
        callback: (tenant: ITenant) => this.activateTenant(tenant),
        showIf: (tenant: ITenant) => this.isSuspended(tenant)
      },
      {
        type: 'suspendTenantAction',
        icon: 'power-off',
        text: gettext('Suspend`tenant`'),
        callback: (tenant: ITenant) => this.suspendTenant(tenant),
        showIf: (tenant: ITenant) => this.isActive(tenant)
      },
      {
        type: BuiltInActionType.Delete,
        text: gettext('Delete`tenant`'),
        callback: tenant => this.delete(tenant),
        showIf: () => this.isManagementTenant
      }
    ];
  }

  createTenant() {
    this.location.go('/tenants/new');
  }

  goToDetails(tenant: ITenant) {
    this.location.go(`/tenants/${tenant.id}`);
  }

  async activateTenant(tenant) {
    try {
      const { data: savedTenant } = await this.tenantService.update({
        id: tenant.id,
        status: TenantStatus.ACTIVE
      });
      Object.assign(tenant, savedTenant);
      this.alertService.success(gettext('Tenant activated.'));
    } catch (ex) {
      if (ex) {
        this.alertService.addServerFailure(ex);
      }
    }
  }

  async suspendTenant(tenant) {
    const title = gettext('Suspend tenant');
    const confirmationText = gettext(
      'You are about to suspend tenant "{{ company }}" (ID "{{ id }}").'
    );
    const proceed = gettext('Do you want to proceed?');
    const body = [
      this.translateService.instant(confirmationText, {
        company: tenant.company,
        id: tenant.id
      }),
      this.translateService.instant(proceed)
    ].join(' ');
    const labels = {
      ok: gettext('Suspend`tenant`')
    };
    try {
      await this.modalService.confirm(title, body, Status.DANGER, labels);
      const confirmed = await this.passwordService.confirmPassword().toPromise();
      if (confirmed === true) {
        const { data: savedTenant } = await this.tenantService.update({
          id: tenant.id,
          status: TenantStatus.SUSPENDED
        });
        Object.assign(tenant, savedTenant);
        this.alertService.success(gettext('Tenant suspended.'));
      }
    } catch (ex) {
      if (ex) {
        this.alertService.addServerFailure(ex);
      }
    }
  }

  async delete(tenant) {
    const title = gettext('Delete tenant');
    const confirmationText = gettext(
      'You are about to delete tenant "{{ company }}" (ID "{{ id }}").'
    );
    const hint = gettext('This operation is irreversible.');
    const proceed = gettext('Do you want to proceed?');
    const body = [
      this.translateService.instant(confirmationText, {
        company: tenant.company,
        id: tenant.id
      }),
      this.translateService.instant(hint),
      this.translateService.instant(proceed)
    ].join(' ');
    const labels = {
      ok: gettext('Delete`tenant`')
    };
    try {
      await this.modalService.confirm(title, body, Status.DANGER, labels);
      const confirmed = await this.passwordService.confirmPassword().toPromise();
      if (confirmed === true) {
        await this.tenantService.delete(tenant);
        const tenantsWithoutRemovedOne = this.tenants$.value.filter(t => t !== tenant);
        this.tenants$.next(tenantsWithoutRemovedOne);
        this.alertService.success(
          gettext('Tenant is being deleted in the background. This might take a while…')
        );
      }
    } catch (ex) {
      if (ex) {
        this.alertService.addServerFailure(ex);
      }
    }
  }

  isActive(tenant: ITenant) {
    return tenant.status === TenantStatus.ACTIVE;
  }

  isSuspended(tenant: ITenant) {
    return tenant.status === TenantStatus.SUSPENDED;
  }

  async downloadNewsletterEmails() {
    const { res, data } = await this.userService.getNewsletterEmails();
    const contentType = res.headers.get('content-type');
    const contentDisposition = res.headers.get('content-disposition');
    const filename = /filename="(.*)"/.exec(contentDisposition)[1];
    const blob = new Blob([data], { type: contentType });
    saveAs(blob, filename);
  }
}
